<template lang="html">
  <div class="Message-detail">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="4" class="font-16">
          <h3>已发布详情</h3>
        </Col>
        <Col span="8">

        </Col>
        <Col span="8">

        </Col>
        <Col span="4">
          <Button type="warning" class="mgl-20" @click="cancel">删除</Button>
        </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">发布日期：</Col>
      <Col span="6" class="mgl-20 text-left"> 2018-3-01 10:20:30</Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">标题：</Col>
      <Col span="6" class="mgl-20"> <Input v-model="ContractName" placeholder="" style="width: 300px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-edit">
      <Col span="4" class="text-right">内容：</Col>
      <Col span="14" class="mgl-20">
        <div ref="editor" style="text-align:left"><p>{{ editorContent }}</p></div>
        <!-- <button @click="getContent">查看内容</button> -->
      </Col>
    </Row>
    
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data (){
    return {
      ContractName:'普及评级方法  提升评级效率',
      ContractUse:'',
      editorContent: '普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率普及评级方法  提升评级效率',
    }
  },
  methods:{
    succ () {
      this.$Message.success('保存成功');
      setTimeout(() =>{
        this.$router.push('/Release_message');
      },2000)
    },
    getContent: function () {
        alert(this.editorContent)
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.ContractName = routerParams.name
          this.ContractUse = routerParams.use
          this.editorContent = routerParams.content
        }
    },
    cancel () {
      this.$router.push('/Release_message');
    }
  },
  mounted() {
    var editor = new E(this.$refs.editor)
    editor.customConfig.onchange = (html) => {
      //this.editorContent = html
    }
    editor.create()
    this.getParams ()
    if(this.isban == true){
      editor.$textElem.attr('contenteditable', false)
    }

  }
}
</script>

<style lang="css">
.Message-detail{
  background-color: #fff;
  padding: 20px;
}
.Message-detail .search_criteria{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: center;
}
.Message-detail .contract-row{
  line-height: 50px;
  margin-bottom: 20px;
}
.Message-detail .ivu-select-dropdown{
  z-index: 99999
}
</style>



